.player-content {
  position: absolute;
  left: 0;
  top: 0;
  padding: 0 1.25rem;
  width: calc(100% - 2.5rem);
  transition: all 0.3s;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.music-cover {
  width: 100%;
  height: 50%;
  padding: 15px 0;
  max-height: calc(100% - 240px);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.music-cover img {
  min-width: 18.75rem;
  width: auto;
  object-fit: cover;
  border-radius: 50%;
  margin: 0 auto;
}
.music-controll {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 25px;
}
.music-controll .music-title {
  font-size: 25px;
  color: #030303;
  height: 35px;
  line-height: 35px;
  text-align: center;
}
.music-controll .music-artist {
  font-size: 15px;
  color: #4a4a4a;
  height: 30px;
  line-height: 30px;
}
.music-controll .music-time {
  font-size: 14px;
  color: #999999;
  height: 25px;
  line-height: 25px;
}
.music-controll .music-vol-ptype {
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.music-controll .music-vol-ptype .play-type {
  display: flex;
  width: 70px;
  height: 100%;
  justify-content: space-between;
}
.music-controll .music-vol-ptype .repeatType {
  display: block;
  font-size: 20px;
  padding: 5px;
  cursor: pointer;
}
.music-controll .music-vol-ptype .music-list {
  display: block;
  font-size: 25px;
  padding: 5px;
  cursor: pointer;
}
.music-controll .music-vol-ptype .volume-container {
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.music-controll .music-vol-ptype .volume-container .volume-wrapper {
  width: 130px;
  display: flex;
  align-items: center;
}
.music-controll .music-progress {
  display: flex;
  align-items: center;
  height: 20px;
  line-height: 20px;
}
.music-controll .music-ctrl {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}
.music-controll .music-ctrl .iconfont {
  display: block;
  width: 30px;
  height: 30px;
  font-size: 30px;
  cursor: pointer;
}
